
body{
  margin: 0px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 20px;
}

/* you can assign all of them to have same style*/
/*h1, h2, h3, h4, h5, h6 {
  font-weight: lighter;
}*/
.info {
  color: inherit;
  text-decoration: none;
  display: block;
  margin: 0px;
  text-align: center;
}
.subscription-style{
    /*padding: 10px 30px;*/
    color: #FF5C5C;
    text-align: center;
}
.hero-image {
  /*width: 200px;*/
  max-height: 300px;
  width: auto ;
  max-width: 100% ;
  height: auto ;
}
.hero-banner {
  background-color: #FF5C5C;
  padding: 50px;
  color: #EDEDED;
  text-align: center;
  background-image: linear-gradient(to bottom,#FF5C5C 0,#f98181 100%);
}
.hero-text {
  font-weight: normal;
}
.app-banner {
  max-width: 700px;
  margin: auto;
}
.app-description {
  background-color: #EDEDED;
  padding: 30px;
  text-align: center;
  color: #31323A;
}
.app-images {
  text-align: center;
}
.app-image-wrapper {
  display: inline-block;
  max-width: 220px;
  padding: 60px;
}
.app-image {
  /* Image inherits the parent size which is the app-image-wrapper*/
  width: 100%;
}
.about-image {
  max-width: 220px;
  padding: 60px;
}
.app-text {
  font-weight: normal;
}
.footer {
  background: #FF5C5C;
  color: #EDEDED;
  padding: 10px 40px 20px;
}




